import { Component } from '@angular/core';
import { IonicPage, NavController, Alert, AlertController, NavParams } from 'ionic-angular';
import { EventProvider } from '../../providers/event/event';
@IonicPage({
  segment: 'event-detail/:eventId'
})
@Component({
  selector: 'page-editar-anuncio',
  templateUrl: 'editar-anuncio.html',
})
export class EditarAnuncioPage {
  public currentEvent: any = {};
  
      constructor(public navCtrl: NavController,  public alertCtrl: AlertController, public navParams: NavParams, 
          public eventProvider: EventProvider) 
          { 
              this.currentEvent = this.eventProvider.getEventDetail;
          }
  ionViewDidLoad() {
    this.eventProvider.getEventDetail(this.navParams.get('eventId')).on('value', eventSnapshot => {
      this.currentEvent = eventSnapshot.val();
      this.currentEvent.id = eventSnapshot.key;
    });
  }

/*   goToEditarAnuncio(): void {
    this.navCtrl.push('EditarAnuncioPage')
  } */
  updateVagaName(): void {
    const alert: Alert = this.alertCtrl.create(
      {
        message: "Título do anúncio",
        inputs: [
          {
            name: 'Nome',
            placeholder: 'Título do anúncio',
            value: this.currentEvent.Nome  //
          },
        ],
        buttons: [
          { text: 'Cancel' },
          {
            text: 'Save',
            handler: data => {
              this.eventProvider.updateVagaName(data.Nome, this.currentEvent.id); //eventProvider
            }
          }
        ]
      });
    alert.present();
  }

  updateVagaEspecifica(): void {
    const alert: Alert = this.alertCtrl.create(
      {
        message: "Especificações do anúncio",
        inputs: [
          {
            name: 'Especificacoes',
            placeholder: 'Especificações do anúncio',
            value: this.currentEvent.Especificacoes
          },
        ],
        buttons: [
          { text: 'Cancel' },
          {
            text: 'Save',
            handler: data => {
              this.eventProvider.updateVagaEspecifica(data.Especificacoes, this.currentEvent.id);
            }
          }
        ]
      });
    alert.present();
  }

  updateVagaPreco(): void {
    const alert: Alert = this.alertCtrl.create(
      {
        message: "Preço do anúncio",
        inputs: [
          {
            name: 'Preco',
            placeholder: 'Preço do anúncio',
            value: this.currentEvent.Preco
          },
        ],
        buttons: [
          { text: 'Cancel' },
          {
            text: 'Save',
            handler: data => {
              this.eventProvider.updateVagaPreco(data.Preco, this.currentEvent.id);
            }
          }
        ]
      });
    alert.present();
  }


  updateVagaEndereco(): void {
    const alert: Alert = this.alertCtrl.create(
      {
        message: "Endereço da vaga",
        inputs: [
          {
            name: 'Endereco',
            placeholder: 'Endereço do anúncio',
            value: this.currentEvent.Endereco
          },
        ],
        buttons: [
          { text: 'Cancel' },
          {
            text: 'Save',
            handler: data => {
              this.eventProvider.updateVagaEndereco(data.Endereco, this.currentEvent.id);
            }
          }
        ]
      });
    alert.present();
  }

  updateVagaLargura(): void {
    const alert: Alert = this.alertCtrl.create(
      {
        message: "Largura da vaga",
        inputs: [
          {
            name: 'Largura',
            placeholder: 'Largura da vaga',
            value: this.currentEvent.Largura
          },
        ],
        buttons: [
          { text: 'Cancel' },
          {
            text: 'Save',
            handler: data => {
              this.eventProvider.updateVagaLargura(data.Largura, this.currentEvent.id);
            }
          }
        ]
      });
    alert.present();
  }

  updateVagaComprimento(): void {
    const alert: Alert = this.alertCtrl.create(
      {
        message: "Comprimento da vaga",
        inputs: [
          {
            name: 'Comprimento',
            placeholder: 'Comprimento da vaga',
            value: this.currentEvent.Comprimento
          },
        ],
        buttons: [
          { text: 'Cancel' },
          {
            text: 'Save',
            handler: data => {
              this.eventProvider.updateVagaComprimento(data.Comprimento, this.currentEvent.id);
            }
          }
        ]
      });
    alert.present();
  }


  updateVagaDataInicial(): void {
    const alert: Alert = this.alertCtrl.create(
      {
        message: "Data Inicial",
        inputs: [
          {
            name: 'Data_inicial',
            placeholder: 'Data Inicial',
            value: this.currentEvent.Data_inicial   // FALTOU COLOCAR CALENDARIO
          },
        ],
        buttons: [
          { text: 'Cancel' },
          {
            text: 'Save',
            handler: data => {
              this.eventProvider.updateVagaDataInicial(data.Data_inicial, this.currentEvent.id);
            }
          }
        ]
      });
    alert.present();
  }

  updateVagaDataFinal(): void {
    const alert: Alert = this.alertCtrl.create(
      {
        message: "Data Final",
        inputs: [
          {
            name: 'Data_final',
            placeholder: 'Data Inicial',
            value: this.currentEvent.Data_final   // FALTOU COLOCAR CALENDARIO
          },
        ],
        buttons: [
          { text: 'Cancel' },
          {
            text: 'Save',
            handler: data => {
              this.eventProvider.updateVagaDataFinal(data.Data_final, this.currentEvent.id);
            }
          }
        ]
      });
    alert.present();
  }

  updateVagaNumeroVagas(): void {
    const alert: Alert = this.alertCtrl.create(
      {
        message: "Número de Vagas",
        inputs: [
          {
            name: 'Numero_de_vagas',
            placeholder: 'Número de Vagas',
            value: this.currentEvent.Numero_de_vagas
          },
        ],
        buttons: [
          { text: 'Cancel' },
          {
            text: 'Save',
            handler: data => {
              this.eventProvider.updateVagaNumeroVagas(data.Numero_de_vagas, this.currentEvent.id);
            }
          }
        ]
      });
    alert.present();
  }


}



